import React, { Component } from "react";
import FontAwesome from 'react-fontawesome';
 
class Blog extends Component {
  render() {
    return (
      <div>
        <FontAwesome className='super-crazy-colors' name='rocket' size='2x' spinstyle={{ textShadow: '0 1px 0 rgba(0,0,0,0.1)'}}/> Blogs
        <h2>My Personal Website</h2>
        <h3>3/04/2018</h3>
        <p>This paragraph tag is currently static until I can implement it dynamically.</p>
        <p>It has been years since I've written and built my own website. This website will be created using a variety of different framework and languages.</p>
        <p>To start out, I wanted to create a SPA (Single Page Application) website. So to start, I had to figure out if React has maybe router package. It does and it is 
        really easy to implement. Install the React-Router package from NPM and reference all your route on the Main file.
        Now that I have my tab all seperately routed, it all comes down to the styling and implementing different tools in my app. I wanted to use Pugs (fka Jade) in my applications but I am still
        having trouble getting it to work correctly. I have been using CoffeeScript the last couple of months and at first I hated it but now that I'm so use to it, I wanted to implement it in this app.
        All this is just the beginning tip of the iceberg, I am going soon implement Redux and other tools. But as for now, I wanted to blog on where I am on the website. Who knows, once this is finish.. I might move on creating a native React app. </p>
      </div>
    );
  }
}

export default Blog;



