import React, { Component } from "react";
import { BrowserRouter, Route, Link } from 'react-router-dom';
import FontAwesome from 'react-fontawesome';
import Home from "./Home";
import AboutMe from "./AboutMe";
import Contact from "./Contact";
import Blog from "./Blog";
import "./index.css";

class Main extends Component {
  render() {
    return (
      <BrowserRouter>
        <div>
          <ul className="header">
            <li><Link to="/"><FontAwesome name='home' size='2x'/>  Home</Link></li>
            <li><Link to="/aboutMe"><FontAwesome name='user' size='2x'/>  About Me</Link></li>
            <li><Link to="/blog"><FontAwesome name='edit' size='2x'/>  Blogs</Link></li>
            <li><Link to="/contact"><FontAwesome name='address-book' size='2x'/>    Contact</Link></li>
          </ul>
          <div className="content">
            <Route exact path="/" component={Home}/>
            <Route path="/aboutMe" component={AboutMe}/>
            <Route path="/blog" component={Blog}/>
            <Route path="/contact" component={Contact}/>
          </div>
        </div>
      </BrowserRouter>
    );
  }
}
 
export default Main;


