import React, { Component } from 'react';
import buttonStyle from './buttonStyle';

class Button extends Component {
  render () {
    return (
      <button
        className="btn btn-default"
        style={buttonStyle}
        onClick={this.props.handleClick}>{this.props.label}</button>
    );
  }
};

export default Button;


