import React, { Component } from 'react';
import LabelComponent from './components/Label/labelComponent';
import frontend from './images/frontend-backend.jpg';
class Home extends Component {
  constructor(props){
    super(props);
  }
  render() {
    return (
      <div>
        <h1>The Tran is a full stack developer, software engineer that currently reside in Dallas, TX</h1>
        <img src={frontend} className="thumb"/>
        <h2>Introduction</h2>
        <p>This is a SPA (Single Page Application) website that I will be building by using ReactJS along with the following tools.</p>
        <ul>
        <li>CoffeeScript</li>
        <li>Pug</li>
        <li>ReactJS</li>
        <li>Babel</li>
      </ul>
      </div>
    );
  }
}

export default Home;



